html, body{
    height: 100%;
    overflow: hidden;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
    --animation-speed: 200ms;
}

#graphic{
    height: 1fr;
    width: 1fr;
    grid-row: 1/3;
    overflow-y: scroll;
}
@-webkit-keyframes fadein{
    from{
        opacity: 0;
    }to{
        opacity: 1;
    }
}
@keyframes fadein{
    from{
        opacity: 0;
    }to{
        opacity: 1;
    }
}
.node{
    position: fixed;
    -webkit-animation: fadein 1s;
            animation: fadein 1s;
    font-family: 'Source Code Pro', monospace;
    height: 70px;
    width: 70px;
    border-radius: 35px;
    background-image: linear-gradient(to bottom, #ff4be7, #8e38ff);
    z-index: 100;
    transition: all 200ms;
    transition: all var(--animation-speed);
}
.node-span{
    position: absolute;
    height: 58px;
    width: 58px;
    margin: 6px 0 0 6px;
    font-size: 26px;
    box-sizing: border-box;
    padding-top: 12px;
    text-align: center;
    border-radius: 30px;
    background-color: #151515;
    box-shadow: 0 0 5px 5px #151515;
    transition: all 500ms;
    color: white;
}
.node-span:hover{
    box-shadow: 0 0 5px 2px #151515;
}
.control-header{
    font-family: 'Raleway', sans-serif;
    text-align: center;
}
.bst-controls{
    text-align: center;
    padding: 20px;
    display: grid;
    grid-row-gap: 20px;
    row-gap: 20px;
    grid-template-columns: calc(100% - 56px) 56px;
}
.no-margin{
    margin: 0;
}
svg{
    position: fixed;
}
.node-link{
    stroke-width: 3px;
    stroke: white;
    z-index: 500;
}
.visualizer{
    background-color: #151515;
    height: 100vh;
    width: 100vw;
    box-sizing: border-box;
    padding: 20px;
    display: grid;
    grid-template-columns: 1fr 220px;
    grid-template-rows: 400px 50%;
    grid-column-gap: 20px;
    -webkit-column-gap: 20px;
            column-gap: 20px;
    grid-row-gap: 20px;
    row-gap: 20px;
}

